.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

img {
  display: block;
}

.map iframe {
  border: none;
  display: block;
}

@media (max-width: @mobile-width-only) {
  br.title-br {
    display: none;
  }
}

@media (min-width: @tablet-width) {
  br:not(.title-br) {
    display: none;
  }
}

@media (min-width: @desktop-width) {
  br:not(.title-br) {
    display: none;
  }
}
